.prizes {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 14.6rem 0 7.7rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 146px 0 65px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    padding: 5.9rem 0;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 9rem 0 6rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 5rem 0 1.5rem;
  }
}

.prizes__title {
  margin: 0 0 6rem;
  text-transform: uppercase;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;

  @media (min-width: $stop-scaling) {
    font-size: 48px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 3rem;
    margin-bottom: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.prizes__lead {
  margin-left: 5.8rem;
  margin-bottom: -5.6rem;
  max-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;

  @media (min-width: $stop-scaling) {
    margin-left: 58px;
    margin-bottom: -56px;
    max-width: 340px;
    font-size: 24px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    margin-left: 2.5rem;
    margin-bottom: 3.2rem;
    max-width: 22rem;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-left: 2rem;
    font-size: 2rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    max-width: 20rem;
    font-size: 1.4rem;
  }

  p {
    margin: 0;
  }
}

.prizes__list {
  @include list-reset;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  margin-top: auto;

  @media (max-width: $tablet) and (orientation: portrait) {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin: auto 0;
    padding-left: 2rem;
  }
}

.prizes__item {
  margin-left: 7.5rem;

  @media (min-width: $stop-scaling) {
    margin-left: 75px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: flex;
    align-items: flex-end;
    margin-left: 0;
    margin-bottom: 2.8rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-left: 5rem;
  }

  &--journeys {
    transform: translate3d(100%, 0, 0);
    animation: slide 0.8s 3.5s forwards;

    @media (max-width: $tablet) and (orientation: portrait) {
      transform: translate3d(0, 0, 0);
    }

    .prizes__desc-counter {
      opacity: 0;
      animation: show 0.2s 3s forwards;
    }

    .prizes__desc-text {
      opacity: 0;
      transform: translate3d(10%, 0, 0);
      animation: show 0.4s 3s forwards, slide 0.4s 3s forwards;
    }

    .prizes__icon {
      position: relative;
      left: -5.9rem;
      bottom: -0.5rem;
      width: 26.8rem;

      @media (min-width: $stop-scaling) {
        left: -59px;
        bottom: -6px;
        width: 268px;
      }

      @media (max-width: $tablet) and (orientation: portrait) {
        left: 0;
        bottom: 0;
        width: 9.4rem;
      }

      @media (max-width: $tablet) and (orientation: landscape) {
        left: -2rem;
        width: 13rem;
      }

      @media (max-width: $mobile) and (orientation: landscape) {
        width: 10rem;
      }
    }
  }

  &--cases {
    margin-left: 7rem;
    opacity: 0;
    animation: show 0.6s 3.7s forwards;

    @media (min-width: $stop-scaling) {
      margin-left: 70px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin-left: 0;
      padding-left: 1.5rem;
    }

    .prizes__desc-counter {
      opacity: 0;
      animation: show 0.2s 3.8s forwards;
    }

    .prizes__desc-text {
      opacity: 0;
      transform: translate3d(10%, 0, 0);
      animation: show 0.4s 4s forwards, slide 0.4s 4s forwards;
    }

    .prizes__icon {
      width: 18.8rem;

      @media (min-width: $stop-scaling) {
        width: 188px;
      }

      @media (max-width: $tablet) and (orientation: portrait) {
        margin-right: 1.3rem;
        width: 10.211rem;
      }

      @media (max-width: $tablet) and (orientation: landscape) {
        width: 9.5rem;
      }

      @media (max-width: $mobile) and (orientation: landscape) {
        width: 7rem;
      }
    }
  }

  &--codes {
    opacity: 0;
    animation: show 0.6s 4.7s forwards;

    @media (max-width: $tablet) and (orientation: portrait) {
      padding-left: 0.8rem;
    }

    .prizes__desc-counter {
      opacity: 0;
      animation: show 0.2s 4.9s forwards;
    }

    .prizes__desc-text {
      opacity: 0;
      transform: translate3d(10%, 0, 0);
      animation: show 0.4s 5s forwards, slide 0.4s 5s forwards;
    }

    .prizes__icon {
      width: 26rem;

      @media (min-width: $stop-scaling) {
        width: 260px;
      }

      @media (max-width: $tablet) and (orientation: portrait) {
        width: 8.66rem;
      }

      @media (max-width: $tablet) and (orientation: landscape) {
        width: 13rem;
      }

      @media (max-width: $mobile) and (orientation: landscape) {
        width: 10rem;
      }
    }
  }
}

.prizes__icon {
  @media (max-width: $tablet) and (orientation: portrait) {
    margin-right: 3.8rem;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.prizes__desc {
  display: flex;
  align-items: flex-end;
  margin: 5.6rem 0 0 0;
  font-size: 1.8rem;
  line-height: 1.1;

  @media (min-width: $stop-scaling) {
    margin-top: 56px;
    font-size: 18px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 0;
    margin-bottom: -0.5rem;
    margin-left: 0;
    font-size: 1.4rem;
    line-height: 1.5rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    margin-top: 3rem;
    font-size: 1.1rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 0.8rem;
  }

  .prizes__desc-counter {
    font-family: $font-alt;
    font-weight: 400;
    font-size: 12rem;
    line-height: 0.55;

    @media (min-width: $stop-scaling) {
      font-size: 120px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      font-size: 4.8rem;
      line-height: 0.6;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      font-size: 6rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      font-size: 3.5rem;
    }
  }

  .prizes__desc-text {
    display: flex;
    align-items: flex-end;
    margin-left: 1.4rem;
    max-width: 14rem;

    @media (min-width: $stop-scaling) {
      margin-left: 14px;
      max-width: 140px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin-left: 0;
      max-width: 11rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-left: 1rem;
      max-width: 13rem;
    }
  }

  svg {
    flex: none;
    margin-left: 1rem;
    margin-bottom: 0.3rem;
    width: 1rem;
    height: 1rem;

    @media (min-width: $stop-scaling) {
      margin-left: 10px;
      margin-bottom: 3px;
      width: 10px;
      height: 10px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin-left: 0;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 0.5rem;
      height: 0.5rem;
    }
  }
}

.prizes__desc-counter-wrapper {
  position: relative;
  height: 0.55em;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 12rem;
  line-height: 0.55;

  .prizes__item--cases & {
    width: 6rem;
  }

  .prizes__item--codes & {
    width: 20rem;
  }

  @media (min-width: $stop-scaling) {
    font-size: 120px;

    .prizes__item--cases & {
      width: 60px;
    }

    .prizes__item--codes & {
      width: 200px;
    }
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    height: 0.6em;
    font-size: 4.8rem;
    line-height: 0.6;

    .prizes__item--cases & {
      width: 2.7rem;
    }

    .prizes__item--codes & {
      width: 9rem;
    }
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    font-size: 6rem;

    .prizes__item--cases & {
      width: 3rem;
    }

    .prizes__item--codes & {
      width: 10rem;
    }
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 3.5rem;

    .prizes__item--cases & {
      width: 1.5rem;
    }

    .prizes__item--codes & {
      width: 5rem;
    }
  }

  .prizes__desc-counter {
    position: absolute;
    top: 0;
    left: 0;
  }
}

.prizes__airship {
  animation: hide 0.4s 3s forwards;
}

.prizes__columns {
  opacity: 0;
  animation: show 0.2s 3s forwards;
}

.prizes__chair {
  opacity: 0;
  animation: show 0.4s 2.8s forwards;
}

.prizes__fern {
  opacity: 0;
  animation: show 0.3s 2.9s forwards;
}

.prizes__fern-wrapper {
  transform-origin: 150px 330px;
  animation: fern-move 1.5s 2.9s forwards;
}

@keyframes fern-move {
  0% {
    transform: rotate(-20deg);
  }

  25% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(-7deg);
  }

  75% {
    transform: rotate(0);
  }

  90% {
    transform: rotate(-2deg);
  }

  100% {
    transform: rotate(0);
  }
}
